<template>
    <!--  -->
    <div class="sidebar">
    <!-- default-active index
         collapse
         unique-openedfalse
         router vue-router  index  path false -->
        <el-menu
            class="sidebar-el-menu"
            background-color="#ffffff"
            text-color="#2f3542"
            active-text-color="#20a0ff"
            unique-opened
        >
        <!-- items -->
            <el-menu-item-group>
              <el-menu-item @click='goAllFiles'>
                <i class="el-icon-files"></i>
                <span slot="title"></span>
              </el-menu-item>
              <el-menu-item @click='goDocs'>
                <i class="el-icon-document"></i>
                </el-menu-item>
              <el-menu-item @click='goImgs'>
                <i class="el-icon-picture"></i>
                </el-menu-item>
              <el-menu-item @click='goMusics'>
                <i class="el-icon-headset"></i>
                </el-menu-item>
              <el-menu-item @click='goVideos'>
                <i class="el-icon-video-camera-solid"></i>
                </el-menu-item>
              <!-- <el-menu-item @click='goTrash'>
                <i class="el-icon-files"></i>
                </el-menu-item> -->
              <el-menu-item @click='goDemo'>
                <i class="el-icon-files"></i>
                demo</el-menu-item>
              <!-- <el-menu-item @click='goTrash'>
                <el-progress type="circle" :percentage="20"></el-progress>
                2G/10G
              </el-menu-item> -->

            </el-menu-item-group>
        </el-menu>
    </div>
</template>

<script>
    export default {
        data() {
            return {

            };
        },
        computed: {
        },
        created() {
        },
        methods:{
          goAllFiles(){
            this.$router.push('/home/files')
            this.$store.dispatch('clearCurPathItems')
          },
          goDocs(){
            this.$router.push('/home/doc')
          },
          goImgs(){
            this.$router.push('/home/img')
          },
          goMusics(){
            this.$router.push('/home/music')
          },
          goVideos(){
            this.$router.push('/home/video')
          },
          goTrash(){
            this.$router.push('/home')
          },
          goDemo(){
            this.$router.push('/home/demo')
          }
        }
    };
</script>

<!-- CSS -->
<style scoped>
    .sidebar {
        display: block;
        position: absolute;
        left: 0;
        top: 70px;
        bottom: 0;
        overflow-y: scroll;
        background-color: #74b9ff;
        color: #0000FF;
    }
    .sidebar::-webkit-scrollbar {
        width: 0;
    }
    .sidebar-el-menu:not(.el-menu--collapse) {
        width: 250px;
    }
    .sidebar > ul {
        height: 100%;
    }
</style>



