<template>
<!--  -->
    <div class="header">
        <!--  -->
        <div class="collapse-btn" @click="collapseChage">
            <!-- v-ifv-else -->
            <!-- <i v-if="!collapse" class="el-icon-s-fold"></i>
            <i v-else class="el-icon-s-unfold"></i> -->
          <i class="el-icon-cloudy"></i>
        </div>
        <div class="logo">BytesCloud</div>
        <div class="header-right">
            <div class="header-user-con">
                <!--  -->
                <div class="btn-fullscreen" @click="handleFullScreen">
                    <!-- placement -->
                    <el-tooltip effect="dark" :content="fullscreen?``:``" placement="bottom">
                        <i class="el-icon-rank"></i>
                    </el-tooltip>
                </div>
                <!--  -->
                <div class="user-avator">
                    <img src="../../assets/img/img.jpg" />
                </div>
                <!-- trigger click  -->
                <el-dropdown v-if="loggedIn"
                    class="user-name" trigger="click" @command="handleCommand">
                    <span class="el-dropdown-link" style="color:  #2f3542;">
                        {{curEmail}}
                        <i class="el-icon-caret-bottom"></i>
                    </span>
                    <!-- slot -->
                    <el-dropdown-menu slot="dropdown">
                        <a href="https:wangbei.xyz" target="_blank">
                            <el-dropdown-item></el-dropdown-item>
                        </a>
                        <el-dropdown-item divided command="loginout"></el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
                <el-dropdown v-else
                    class="user-name" trigger="click" @command="handleCommand">
                    <span class="el-dropdown-link">
                        /
                        <i class="el-icon-caret-bottom"></i>
                    </span>
                    <!-- slot -->
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item divided command="login"></el-dropdown-item>
                        <el-dropdown-item divided command="register"></el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                collapse: false,
                fullscreen: false,
                name: 'AsunaCC',
            };
        },


    // 
        // computed: {
        //     username() {
        //         // 
        //         let username = localStorage.getItem('ms_username');
        //         //  data 
        //         return username ? username : this.name;
        //     },

        // },
        computed:{
          loggedIn(){
            return this.$store.getters.loggedIn
          },
          curEmail(){
            return this.$store.getters.curEmail
          }
        },

        methods: {
            // 
            handleCommand(command) {
                //
                if (command == 'loginout') {
                   this.$router.push('/logout');
                }else if(command == 'register'){
                  this.$router.push('/register');
                }else if (command == 'login'){
                  this.$router.push('/login');
                }
            },


            // 
            collapseChage() {
                this.collapse = !this.collapse;
                // bus
                // bus.$emit('collapse', this.collapse);
            },


            // 
            handleFullScreen() {
                // 
                let element = document.documentElement;
                if (this.fullscreen) {
                    // 
                    // -webkit- chrome  safari
                    // -moz- firefox
                    // -ms- IE
                    // -o- opera
                    if (document.exitFullscreen) {
                        document.exitFullscreen();
                    } else if (document.webkitCancelFullScreen) {
                        document.webkitCancelFullScreen();
                    } else if (document.mozCancelFullScreen) {
                        document.mozCancelFullScreen();
                    } else if (document.msExitFullscreen) {
                        document.msExitFullscreen();
                    }
                } else {
                    // 
                    if (element.requestFullscreen) {
                        element.requestFullscreen();
                    } else if (element.webkitRequestFullScreen) {
                        element.webkitRequestFullScreen();
                    } else if (element.mozRequestFullScreen) {
                        element.mozRequestFullScreen();
                    } else if (element.msRequestFullscreen) {
                        // IE11
                        element.msRequestFullscreen();
                    }
                }
                this.fullscreen = !this.fullscreen;
            }
        },


        // 1500 collapseChage 
        mounted() {
            if (document.body.clientWidth < 1500) {
                this.collapseChage();
            }
            // bus  message 
            // bus.$on('msg', (e) => {
            //     this.message = e;
            // })
        }
    };
</script>


<!-- CSS -->
<style scoped>
    .header {
        position: relative;
        box-sizing: border-box;
        width: 100%;
        height: 70px;
        font-size: 22px;
        color: #2f3542;
        background-color: #f5f6fa;
        /* color: #78e08f; */
    }
    .collapse-btn {
        float: left;
        padding: 0 21px;
        cursor: pointer;
        line-height: 70px;
    }
    .header .logo {
        float: left;
        width: 250px;
        line-height: 70px;
    }
    .header-right {
        float: right;
        padding-right: 50px;
    }
    .header-user-con {
        display: flex;
        height: 70px;
        align-items: center;
    }
    .btn-fullscreen {
        transform: rotate(45deg);
        margin-right: 5px;
        font-size: 24px;
    }
    .btn-bell,
    .btn-fullscreen {
        position: relative;
        width: 30px;
        height: 30px;
        text-align: center;
        border-radius: 15px;
        cursor: pointer;
    }
    .btn-bell-badge {
        position: absolute;
        right: 0;
        top: -2px;
        width: 8px;
        height: 8px;
        border-radius: 4px;
        background: #f56c6c;
        color: #fff;
    }
    .btn-bell .el-icon-bell {
        color: #fff;
    }
    .user-name {
        margin-left: 10px;
    }
    .user-avator {
        margin-left: 20px;
    }
    .user-avator img {
        display: block;
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
    .el-dropdown-link {
        color: #fff;
        cursor: pointer;
    }
    .el-dropdown-menu__item {
        text-align: center;
    }
</style>



